<template>
  <div>
    <div class="recommend" ref="recommend">
      <scroll ref="scroll" class="recommend-content" :data="discList">
        <div>
          <div>
     <swiper :swiperSlides="sliders" @loadImage="loadImage"></swiper>
          </div>
      <div class="recommend-list">
        <h1 class="list-title">热门歌单推荐</h1>
        <ul>
          <li @click="selectItem(item)" v-for="item in discList" class="item">
            <div class="icon">
              <img v-lazy="item.imgurl">
            </div>
            <div class="text">
              <h2 class="name" v-html="item.creator.name"></h2>
              <p class="desc" v-html="item.dissname"></p>
            </div>
          </li>
        </ul>
      </div>
        </div>
        <div class="loading-container" v-show="!discList.length">
          <loading></loading>
        </div>
      </Scroll>

    </div>
    <router-view></router-view>
  </div>
</template>

<script>
   import Scroll from '../../base/scroll'
   import swiper from '../../base/swiper'
   import {getRecommend} from '../../api/recommend'
   import {ERR_OK} from '../../api/config'
   import {getDiscList} from '../../api/recommend'
   import loading from '../../base/loading'
   import {playlistMixin} from '../../common/js/mixin'
   import {mapMutations} from 'vuex'
   export default {
        name: "recommend",
     mixins:[playlistMixin],
      components:{
          swiper,
          Scroll,
        loading
      },
      created(){

        this.getRecommend();
        this._getDiscList();

      },
      methods: {
        async getRecommend() {
          let sliders = await getRecommend();
          if (sliders.code === ERR_OK) {

            this.sliders = sliders.data.slider;
            // console.log(this.sliders.length);
            // let a=JSON.stringify(this.sliders);
            // console.log(JSON.parse(a));

          }

        },
        selectItem(item){
          this.$router.push({
            path: `/recommend/${item.dissid}`
          })
          this.setDisc(item)
        },
        ...mapMutations({
          setDisc: 'SET_DISC'
        }),
        handlePlayList(playList){
          const bottom =playList.length>0 ? '66px':''
          this.$refs.recommend.style.bottom = bottom
          this.$refs.scroll.refresh()
        },

        _getDiscList() {
          getDiscList().then((res) => {
            if (res.code === ERR_OK) {
              console.log(res.data)
              this.discList = res.data.list
            }
          })
        },
        loadImage(checkImage){
          if(checkImage){
            this.$refs.scroll.refresh()
          }
        },
      },
     data(){
          return {
            sliders:[],
            discList:[]
          }
     }
    }
</script>

<style scoped lang="stylus">
  @import '../../common/stylus/variable.styl'
  .recommend {
    position: fixed;
    width: 100%;
    top: 88px;
    bottom: 0;
    overflow :hidden;
  }
    .recommend-content{
      height: 100%
      overflow: hidden
    }
  .recommend-list{
    display :flex;
    flex-direction column;
    overflow :hidden;
    h1{
      border-left:4px solid #007aff;
      padding-left:8px;
      margin-top :8px;
    }
    ul{
      display :flex;
      flex-direction :column;
      flex:1;
  li{
        display :flex;
        flex-direction:row;
        align-items :center
       img{
        width:60px;
        height:60px;
        margin:10px 16px;
      }
      .text {
        display: flex;
        flex-direction: column;
        height:80px;
        justify-content :center
  h2 {
          font-size: $font-size-medium;
          color: $color-theme;
          height :25px;
        }
        p {
          font-size: $font-size-medium;
          color: #2D2D2D;
        }
      }
      }
    }
  }
  .loading-container{
    position: absolute
    top:50%
    width :100%
    transition :translateY(-50%)
  }
</style>



