<template>
  <swiper v-if="swiperSlides.length>1" :options="swiperOption">
    <swiper-slide v-for="(slide, index) in swiperSlides" :key="index">
      <a :href="slide.linkUrl">
      <img @load="loadImage" :src="slide.picUrl">
      </a>
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
  export default {
    name: 'carousel',
    props:['swiperSlides'],
    data() {
      return {
        swiperOption: {
          pagination: {
            el: '.swiper-pagination'
          },
          loop : true,
          autoplay:true,
        },
        checkImage:true
      }
    },
    methods:{
      loadImage(){
        if(!this.checkloaded){
          this.checkloaded = true;
        this.$emit('loadImage',this.checkImage)
        }
      }
    }
  }
</script>
<style scoped>
  img{
    width: 100%;
  }
</style>



